import { Block } from './block'

export class World {
  constructor(game) {
    const { canvas, context } = game
    this.canvas = canvas
    this.context = context
    const unitLength = canvas.height / 5
    this.unitLength = unitLength
    this.groundHeight = canvas.height - unitLength
    this.blocks = []
  }

  createGround() {
    const numberOfSquaresToDraw = Math.floor(this.canvas.width / this.unitLength) + 1
    for (let i = 0; i < numberOfSquaresToDraw; i++) {
      this.blocks.push(new Block(this.context, {
        x: i * this.unitLength,
        y: this.groundHeight,
        width: this.unitLength,
        height: this.unitLength,
        type: 'ground'
      }))
    }
  }

  createPlatform() {
    const numberOfSquaresToDraw = 3
    for (let i = 0; i < numberOfSquaresToDraw; i++) {
      this.blocks.push(new Block(this.context, {
          x: (i + 2) * this.unitLength,
          y: this.groundHeight - this.unitLength * 2,
          width: this.unitLength,
          height: this.unitLength,
          type: 'platform'
      }))
    }
  }

  init() {
    this.createGround()
    this.createPlatform()
  }

  draw() {
    this.blocks.forEach(block => block.draw())
  }
}
