import { Game } from './game'

const main = () => {
  window.game = new Game(document.querySelector('#game'))
  game.init()
}

const debounce = fn => {
  let timer
  return event => {
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(fn, 100, event)
  }
}

document.addEventListener('DOMContentLoaded', main)