export class Block {
  constructor(context, options) {
    this.context = context
    const { x, y, width, height, type } = options
    this.x = x
    this.y = y
    this.width = width
    this.height = height
    this.type = type
  }

  draw() {
    this.context.strokeRect(this.x, this.y, this.width, this.height)
  }
}